/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #1a1b15;
  color: #FFFFFF;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #FFFFFF;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  color: #FFFFFF;
  font-size: 2rem;
}

@media (min-width: 768px) {
  .contenedor-app {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    min-height: 100vh;
  }
}
.contenedor-app .imagen {
  height: 20rem;
  background-image: url(../img/fondo.jpg);
  background-size: cover;
  background-position: center center;
}
@media (min-width: 768px) {
  .contenedor-app .imagen {
    height: auto;
  }
}
.contenedor-app .app {
  width: 95%;
  margin: 0 auto;
  padding: 3rem 0;
}
.contenedor-app .nombre-pagina {
  text-align: center;
  margin: 5rem 0;
  font-weight: 700;
}
.contenedor-app .descripcion-pagina {
  text-align: center;
}

.busqueda h2 {
  color: #1a1b15;
}
.busqueda .formulario .campo label {
  color: #1a1b15;
  font-size: 2rem;
  font-weight: 700;
}

.nh {
  color: #1a1b15;
}

.ventas {
  list-style: none;
  padding: 0;
}
.ventas li {
  padding: 1rem;
  border-top: 1px solid #1a1b15;
}
.ventas li h3 {
  color: #1a1b15;
}
.ventas li p {
  color: #1a1b15;
  font-weight: 700;
  color: #0da6f3;
}
.ventas li span,
.ventas li .servicio {
  font-weight: 400;
  color: #1a1b15;
}
.ventas li .container-img {
  display: flex;
}
.ventas li .container-img img {
  display: inline-block;
  width: 30%;
  margin: 0 auto;
}
.ventas li .aprobado {
  color: green;
  font-weight: 700;
}
.ventas li .rechazado {
  color: red;
  font-weight: 700;
}
.ventas li .en-espera {
  color: black;
  font-weight: 700;
}

.formulario {
  width: 100%;
}

.campo {
  display: flex;
  margin-bottom: 2rem;
  align-items: center;
}
.campo label {
  flex: 0 0 10rem;
}
.campo input {
  flex: 1;
  border: none;
  padding: 1.25rem;
  border-radius: 1rem;
}

.boton {
  background-color: #FBAFB2;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
}
@media (min-width: 768px) {
  .boton {
    width: auto;
  }
}
.boton:hover {
  background-color: #f97e83;
  cursor: pointer;
}

.alerta {
  padding: 1.25rem;
  color: #FFFFFF;
  width: 100%;
  display: block;
  margin-bottom: 2rem;
  text-align: center;
  font-weight: 700;
  text-transform: uppercase;
}
.alerta.error {
  background-color: #cb0000;
}
.alerta.exito {
  background-color: #329f00;
}

.dashboard {
  background-color: #f3f4f6;
}
@media (min-width: 1024px) {
  .dashboard {
    display: flex;
    height: 100vh;
  }
}
.dashboard .principal {
  flex: 1;
  overflow: scroll;
}
.dashboard .contenido {
  padding: 3rem;
}
.dashboard .contenido .nombre-pagina {
  font-size: 2.8rem;
  color: #1a1b15;
}

.imagenes-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  justify-content: center;
  gap: 1rem; /* Espacio entre las imágenes */
  justify-items: center;
  align-items: center;
}

.container-img-1 {
  width: 80%; /* Ancho deseado de cada imagen */
  height: auto;
  overflow: hidden;
}

.container-img img {
  display: inline-block;
  width: 100%;
  margin: 0 auto;
  object-fit: cover;
}

.sidebar {
  background-color: #1a1b15;
  padding: 2rem;
}
.sidebar .imagen {
  height: 15rem;
  background-image: url(../img/fondo.jpg);
  background-size: cover;
  background-position: center center;
  margin-bottom: 2rem;
}
.sidebar h2 {
  color: #FFFFFF;
  margin: 0;
  text-align: center;
}
.sidebar .sidebar-nav {
  margin-top: 5rem;
}
.sidebar .sidebar-nav a {
  color: #FFFFFF;
  display: block;
  margin-bottom: 2rem;
  padding: 0.7rem;
  transition-property: background-color;
  transition-duration: 300ms;
  font-weight: 700;
  font-size: 2rem;
}
.sidebar .sidebar-nav a:hover, .sidebar .sidebar-nav a.activo {
  background-color: #076493;
  border-radius: 0.5rem;
}

.barra {
  background-color: #FFFFFF;
  padding: 1rem 2rem;
  box-shadow: 1px 12px 9px -13px rgba(0, 0, 0, 0.4);
}
@media (min-width: 768px) {
  .barra {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.barra p {
  color: #1a1b15;
  margin: 0;
  font-size: 1.8rem;
}
.barra p span {
  font-weight: 700;
  color: #0da6f3;
}
.barra .cerrar-sesion {
  background-color: #f64e55;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
  margin: 0;
  font-size: 1.5rem;
  padding: 1rem;
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
  .barra .cerrar-sesion {
    width: auto;
  }
}
.barra .cerrar-sesion:hover {
  background-color: #f41d26;
  cursor: pointer;
}

.formulario_vendedor {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

.campo_form {
  margin-top: 5rem;
  display: flex;
  margin-bottom: 2rem;
  align-items: center;
  justify-content: center;
}
.campo_form label {
  flex: 0 0 10rem;
  color: #1a1b15;
  font-size: 2rem;
}
.campo_form input,
.campo_form textarea {
  flex: 0.7;
  border: none;
  padding: 1.25rem;
  border-radius: 1rem;
  box-shadow: 10px 2px 2px #1a1b15;
}

.boton {
  box-shadow: 5px 5px 5px #1a1b15;
  margin: 5rem auto 0;
  padding: 3rem;
}

.contenedor-botones {
  display: flex;
  justify-content: space-between;
  gap: 15rem;
}

.boton_supervisor {
  display: inline-block;
  width: 100%;
  background-color: red;
  box-shadow: 5px 5px 5px #1a1b15;
  margin: 5rem 5rem 2rem 5rem;
  padding: 2rem;
  color: #FFFFFF;
  text-align: center;
  font-size: 1.5rem;
  border: none;
}

.boton_supervisor-verde {
  display: inline-block;
  width: 100%;
  background-color: green;
  box-shadow: 5px 5px 5px #1a1b15;
  margin: 5rem 5rem 2rem 5rem;
  padding: 2rem;
  color: #FFFFFF;
  text-align: center;
  font-size: 1.5rem;
  border: none;
}

.contenedor-nueva-tarea {
  display: flex;
  justify-content: center;
}
.contenedor-nueva-tarea .agregar-tarea {
  background-color: #0da6f3;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
}
@media (min-width: 768px) {
  .contenedor-nueva-tarea .agregar-tarea {
    width: auto;
  }
}
.contenedor-nueva-tarea .agregar-tarea:hover {
  background-color: #0a85c3;
  cursor: pointer;
}

.modal {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.8);
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  color: #1a1b15;
}
.modal .formulario {
  max-width: 60rem;
  background-color: #FFFFFF;
  padding: 2rem;
  margin: 0 auto;
  transform: translateY(-50vh);
  transition-property: all;
  transition-duration: 500ms;
  transition-timing-function: ease-in-out;
  opacity: 0;
  border-radius: 1rem;
}
.modal .formulario legend {
  font-size: 2rem;
  text-align: center;
  font-weight: 700;
  margin-bottom: 2rem;
  display: block;
}
.modal .formulario label {
  font-weight: 700;
}
.modal .formulario input {
  background-color: #f3f4f6;
}
.modal .formulario input[type=text] {
  border: 1px solird #e1e1e1;
  border-radius: 0.5rem;
}
.modal .formulario .opciones {
  display: flex;
  justify-content: space-between;
}
.modal .formulario .submit-nueva-tarea {
  background-color: #329f00;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
  margin: 0;
}
@media (min-width: 768px) {
  .modal .formulario .submit-nueva-tarea {
    width: auto;
  }
}
.modal .formulario .submit-nueva-tarea:hover {
  background-color: #226c00;
  cursor: pointer;
}
.modal .formulario .cerrar-modal {
  background-color: #cb0000;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
  margin: 0;
}
@media (min-width: 768px) {
  .modal .formulario .cerrar-modal {
    width: auto;
  }
}
.modal .formulario .cerrar-modal:hover {
  background-color: #980000;
  cursor: pointer;
}
.modal .formulario.animar {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  opacity: 1;
}
.modal .formulario.cerrar {
  transform: translateY(-50vh);
  opacity: 0;
}

.tarea {
  margin-top: 5rem;
}
.tarea p {
  color: #1a1b15;
}
.tarea span {
  color: #0da6f3;
  font-weight: 700;
}