@use "base/variables" as v;
@use "base/mixins" as m;

.contenedor-app{
    @include m.tablet {
        display: grid;
        grid-template-columns: repeat(2,1fr);
        min-height: 100vh;
    }

    .imagen{
        height: 20rem;
        background-image: url(../img/fondo.jpg);
        background-size: cover;
        background-position: center center;
        @include m.tablet {
                height: auto;
        }
    }

    .app{
        width: 95%;
        margin: 0 auto;
        padding: 3rem 0;
    }

    .nombre-pagina{
        text-align: center;
        margin: 5rem 0;
        font-weight: v.$bold;
    }
    .descripcion-pagina{
        text-align: center;
    }
}
